import React, { useState, useEffect, useRef } from 'react'
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import { classNames } from "primereact/utils";
import { FaEdit, FaRegTrashAlt } from "react-icons/fa";
import { Calendar } from "primereact/calendar";
import { CTooltip } from "@coreui/react";
import { useFormik } from "formik";
import Team from './Team'
import './users.css'
import { TabView, TabPanel } from 'primereact/tabview';
import { ConfirmPopup, confirmPopup } from 'primereact/confirmpopup';
import { Toast } from 'primereact/toast';
import { Password } from 'primereact/password';
import axios from "axios";
import { AppConstants } from "src/constants/constants";
import { ProgressSpinner } from "primereact/progressspinner";
// import {contractStage} from '../../../utils/masterEnums'
export let usersList;

const Users = () => {
  const [viewMode, setViewMode] = useState(0);
  const [editMode, setEditMode] = useState(0);
  const [activeIndex2, setActiveIndex2] = useState(0);
  const [visible, setVisible] = useState(false);
  const [showspinner, setshowspinner] = useState(false);
  const [usersData, setUsersData] = useState(null);
  const [selectedRowData, setSelectedRowData] = useState(null);
  const toast = useRef(null);
  const status = [
    { name: "ACTIVE", code: "ACT" },
    { name: "INACTIVE", code: "IN-ACT" },
  ];
  const accountTeam = [
    { name: "Ninjas", code: "njs" },
    { name: "Worriors", code: "wrr" },
    { name: "Panthers", code: "pnt" },
    { name: "Lions", code: "lns" },
    { name: "Cheetah", code: "cht" },
  ];
  const userType = [
    { name: 'Recruiter', code: 'PRS' },
    { name: 'Account Head', code: 'AM' },
    { name: 'Team Lead', code: 'RM' },
    { name: 'Sourcing Team', code: 'LDN' },
    { name: 'Manager', code: 'IST' },
    { name: 'Customer', code: 'PRS' }
  ];

  useEffect(() => {
    getAllUsers();
    // contractStage();
  }, [])

  const userFormik = useFormik({
    initialValues: {
      fname: "",
      lname: "",
      email: "",
      mobile: "",
      salary: "",
      userRole: "",
      calendar: "",
      accountTeam: "",
    },
    validate: (data) => {
      let errors = {};

      if (!data.fname) {
        errors.fname = "Please enter first name.";
      }
      if (!data.lname) {
        errors.lname = "Please enter last name.";
      }
      if (!data.email) {
        errors.email = "Please enter email id.";
      }
      if (!data.mobile) {
        errors.mobile = "Please enter phone no.";
      }
      if (!data.salary) {
        errors.salary = "Please enter annual salary.";
      }
      if (!data.userRole) {
        errors.userRole = "Please select user role";
      }
      if (!data.calendar) {
        errors.calendar = "Please select start effective date.";
      }
      if (!data.accountTeam) {
        errors.accountTeam = "Please select accountTeam";
      }

      return errors;
    },
    onSubmit: (data) => {
      viewMode === 1 && editMode === 2 ? updateUser(data) : addNewUser(data);
      userFormik.resetForm();
      setViewMode(0);
    },
  });
  const isUserFormFieldValid = (name) =>
    !!(userFormik.touched[name] && userFormik.errors[name]);
  const getUserFormErrorMessage = (name) => {
    return (
      isUserFormFieldValid(name) && (
        <small className="p-error">{userFormik.errors[name]}</small>
      )
    );
  };

  //===== user updated successfully message
  const showUserAdd = (siverty) => {
    toast.current.show({
      severity: siverty,
      summary: siverty === "success" ? "Added" : "Opps",
      detail:
        siverty === "success"
          ? "User has been successfully Added"
          : "Something went wrong.",
      life: 3000,
    });
  };

  //===== user updated successfully message
  const showUserUpdate = (siverty) => {
    toast.current.show({
      severity: siverty,
      summary: siverty === "success" ? "Updated" : "Opps",
      detail:
        siverty === "success"
          ? "User has been successfully updated"
          : "Something went wrong.",
      life: 3000,
    });
  };


  // ======== add users form =============
  const addUserForm = () => {
    return (
      <form onSubmit={userFormik.handleSubmit}>
        <div className='mx-3'>
          <div className="row">
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <InputText
                  id="fname"
                  value={userFormik.values.fname}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("fname"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="fname">First name</label>
              </span>
              {getUserFormErrorMessage("fname")}
            </div>
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <InputText
                  id="lname"
                  value={userFormik.values.lname}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("lname"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="lname">Last name</label>
              </span>
              {getUserFormErrorMessage("lname")}
            </div>
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <InputText
                  id="email"
                  value={userFormik.values.email}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("email"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="email">Email Id</label>
              </span>
              {getUserFormErrorMessage("email")}
            </div>
          </div>
          <div className="row">
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <InputText
                  id="mobile"
                  value={userFormik.values.mobile}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("mobile"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="mobile">Phone No.</label>
              </span>
              {getUserFormErrorMessage("mobile")}
            </div>
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <Calendar
                  id="calendar"
                  name='calendar'
                  value={userFormik.values.calendar}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("calendar"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="calendar">Effective From</label>
              </span>
              {getUserFormErrorMessage("calendar")}
            </div>
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <InputText
                  id="salary"
                  value={userFormik.values.salary}
                  onChange={userFormik.handleChange}
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("salary"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="salary">Salary (annual CTC)</label>
              </span>
              {getUserFormErrorMessage("salary")}
            </div>
          </div>
          <div className="row">
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <Dropdown
                  id="userRole"
                  inputId="dropdown"
                  value={userFormik.values.userRole}
                  options={userType}
                  onChange={userFormik.handleChange}
                  optionLabel="name"
                  optionValue="code"
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("userRole"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="userRole">User Role</label>
              </span>
              {getUserFormErrorMessage("userRole")}
            </div>
            <div className=" col-sm-12 col-md-4 col-lg-4 mt-4">
              <span className="p-float-label">
                <Dropdown
                  id="accountTeam"
                  inputId="dropdown"
                  value={userFormik.values.accountTeam}
                  options={accountTeam}
                  onChange={userFormik.handleChange}
                  optionLabel="name"
                  className={
                    (classNames({
                      "p-invalid": isUserFormFieldValid("accountTeam"),
                    }),
                      "p-inputtext-sm w-100 borderClass")
                  }
                />
                <label htmlFor="userType">Account Team</label>
              </span>
              {getUserFormErrorMessage("accountTeam")}
            </div>
          </div>
        </div>
        <div className="modal-footer d-flex justify-content-end my-3 mx-3">
          <Button
            label={editMode === 1 ? "Add" : "Update"}
            type="submit"
            className="bg-primary border-0  p-button-md  btn-color p-button-raised"
          />
          <Button
            onClick={() => {
              setViewMode(0);
              userFormik.resetForm();
            }}
            label={"Cancel"}
            style={{ marginLeft: "10px" }}
            className="bg-danger border-0 p-button-md p-button-raised"
          />
        </div>
      </form>
    )
  }


  //==== get all user data from Api Server ======
  const getAllUsers = async (data) => {
    setshowspinner(true);
    await axios
      .get(
        `${AppConstants.Api_BaseUrl}admin/user/getAll`,
        {
          headers: {
            // "Content-Type": "application/x-www-form-urlencoded;",
          },
        }
      )
      .then(
        (res) => {
          const dt = res.data;
          setUsersData(dt);
          usersList = res.data;
          setshowspinner(false);
        },
        (err) => {
          console.log(err);
          setshowspinner(false);
        }
      );
  };

  // === post users api =======
  const addNewUser = async (data) => {
    setshowspinner(true);
    const postData = {
      // accountTeam: [
      //   data.accountTeam
      // ],
      createdDate: new Date(),
      email: data.email,
      firstName: data.fname,
      lastName: data.lname,
      password: Math.floor(Math.random() * 10000000000),
      phoneNumber: data.mobile,
      // role: data.userType,
      role: "Manager",
      salary: data.salary,
      status: "Active",
      updatedDate: new Date(),
    }

    await axios
      .post(`${AppConstants.Api_BaseUrl}admin/user/create`, postData, {
        header: {
          // Authorization: `Bearer ${tokenInfo.access_token}`,
        }
      })
      .then((res) => {
        const dt = res.data;
        getAllUsers();
        setshowspinner(false);
        showUserAdd("success");
      },
        (err) => {
          console.log(err);
          setshowspinner(false);
          showUserAdd("error");
        }
      )
  }

  // === put users API =====
  const updateUser = async (data) => {
    setshowspinner(true);
    const postData = {
      // accountTeam: [
      //   "string"
      // ],
      createdBy: selectedRowData.id,
      createdDate: selectedRowData.updatedDate,
      email: data.email,
      firstName: data.fname,
      id: selectedRowData.id,
      lastName: data.lname,
      password: selectedRowData.password,
      phoneNumber: data.mobile,
      role: selectedRowData.role,
      salary: data.salary,
      status: selectedRowData.status,
      updatedBy: selectedRowData.id,
      updatedDate: new Date(),
    }

    await axios
      .put(`${AppConstants.Api_BaseUrl}admin/user/update`, postData, {
        header: {
          // Authorization: `Bearer ${tokenInfo.access_token}`,
        }
      })
      .then((res) => {
        getAllUsers();
        setshowspinner(false);
        showUserUpdate("success");
      },
        (err) => {
          console.log(err);
          setshowspinner(false);
          showUserUpdate("error");
        }
      )
  }


  // ============Code for get Status==========//
  const getStatus = (rowdata) => {
    return (
      <span
        // onClick={(event) => statusConfirmation(event, rowdata)}
        className={
          rowdata.status === "Active" ? "badge bg-success" : "badge bg-danger"
        }
      >
        {rowdata.status}
      </span>
    );
  };

  const renderHeader1 = () => {
    return (
      <div className="row d-flex">
        <div className="col-lg-8">
          <form
          // onSubmit={searchFormik.handleSubmit}
          >
            <div className="row gap-lg-0 mt-lg-0">
              <div className="col-sm-12 col-lg-4 mb-1">
                <span className="p-input-icon-left w-100">
                  <i className="pi pi-search" />
                  <InputText
                    // value={globalFilterValue1}
                    // onChange={onGlobalFilterChange1}
                    placeholder="Search name"
                    className="p-inputtext-sm w-100"
                    optionLabel="name"
                    optionValue="name"
                    filter={false}
                  />
                </span>
              </div>
              <div className="col-sm-12 col-lg-4 mb-1">
                <Dropdown
                  id="status"
                  name="status"
                  options={status}
                  // value={searchFormik.values.status}
                  // onChange={searchFormik.handleChange}
                  className="p-inputtext-sm w-100"
                  optionLabel="name"
                  optionValue="name"
                  filter={false}
                  placeholder="Status"
                />
              </div>
            </div>
          </form>
        </div>
        <div
          className="col-sm-12 col-md-12 col-lg-4 d-flex "
          style={{ justifyContent: "right" }}
        >
          <Button
            label="Add User"
            className="p-button-outlined p-button-primary p-button-md"
            onClick={() => {
              setEditMode(1);
              setViewMode(1);
            }}
          />
          <Button
            style={{
              marginLeft: "10px",
            }}
            role="button"
            icon="pi pi-filter-slash"
            label="Clear"
            className="p-button-outlined p-button-md"
          // onClick={() => {
          //   searchFormik.resetForm();
          //   initFilters1();
          //   if (pageNumber !== 1) {
          //     setPageNumber(1);
          //   } else {
          //     getAllAccounts({
          //       dateRange: null,
          //       status: "",
          //       page: 1,
          //     });
          //   }
          // }}
          />
        </div>
      </div>
    );
  };
  const header1 = renderHeader1();

  const rowClickHandler = (rowdata) => {
    const dt = rowdata.data;
    setViewMode(1);
    setEditMode(2);
    setSelectedRowData(rowdata.data);
    userFormik.setFieldValue("fname", dt.firstName)
    userFormik.setFieldValue("lname", dt.lastName)
    userFormik.setFieldValue("email", dt.email)
    userFormik.setFieldValue("mobile", dt.phoneNumber)
    userFormik.setFieldValue("salary", dt.salary)
    // userFormik.setFieldValue("userRole", dt.role)
  }

  return (
    <div className="row mb-2">
      <Toast ref={toast} />
      <ConfirmPopup />
      <div className="col-sm-12 rounded">
        <div className="card">
          <TabView>
            <TabPanel header="Users">
              {viewMode !== 0 ? (addUserForm()) : (
                <div className="body">
                  <DataTable
                    value={usersData}
                    responsiveLayout="scroll"
                    className="p-datatable-customers"
                    showGridlines={false}
                    rows={10}
                    stripedRows
                    dataKey="id"
                    paginator
                    // filters={filters1}
                    // loading={accountLoading}
                    filterDisplay="menu"
                    globalFilterFields={[
                      "email",
                      "user",
                      "role",
                      "updatedDate",
                      "status",
                    ]}
                    header={header1}
                    onRowClick={rowClickHandler}
                    // onRowClick={() => {
                    //   setViewMode(1);
                    //   setEditMode(2)
                    // }}
                    emptyMessage="No audit logs found."
                  >
                    <Column
                      field="name"
                      header="Name"
                      body={(rowdata) => {
                        return (
                          <span>{rowdata.firstName + " " + rowdata.lastName}</span>
                        )
                      }}
                      style={{ cursor: "pointer" }}
                      sortable
                    ></Column>
                    <Column
                      header="Role"
                      field="role"
                      style={{ cursor: "pointer" }}
                      sortable
                    ></Column>
                    <Column
                      field="phoneNumber"
                      header="Phone No."
                      style={{ cursor: "pointer" }}
                      sortable
                    ></Column>
                    <Column
                      field="email"
                      header="Email"
                      style={{ cursor: "pointer" }}
                      sortable
                    ></Column>
                    <Column
                      field="team"
                      header="Team"
                      body={(rowdata) => rowdata.team != undefined ? rowdata.team : "N/A"}
                      style={{ cursor: "pointer" }}
                      sortable
                    ></Column>
                    {/* <Column
                      field="status"
                      header="Status"
                      style={{ cursor: "pointer" }}
                      body={getStatus}
                      sortable
                    ></Column> */}
                  </DataTable>
                </div>
              )}
            </TabPanel>
            <TabPanel header="Account Team">
              <Team />
            </TabPanel>
          </TabView>

        </div>
      </div>
      {showspinner && (
        <div className="spinner-div">
          <ProgressSpinner />
        </div>
      )}
    </div>
  )
}

export default Users