import React, { useEffect, useState } from 'react'
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import { classNames } from "primereact/utils";
import { FaEdit } from "react-icons/fa";
import { Calendar } from "primereact/calendar";
import { CTooltip } from "@coreui/react";
import { useFormik } from "formik";
import { MultiSelect } from 'primereact/multiselect';
import axios from 'axios';
import { AppConstants } from "src/constants/constants";
import { ProgressSpinner } from "primereact/progressspinner";
import {usersList} from './Users';
import {customersList} from '../customers/Customers'


const Team = () => {
    const [viewMode, setViewMode] = useState(0);
    const [editMode, setEditMode] = useState(0);
    const [teamData, setTeamData] = useState(null);
    const [showspinner, setshowspinner] = useState(false);
    const status = [
        { name: "ACTIVE", code: "ACT" },
        { name: "INACTIVE", code: "IN-ACT" },
    ];

    const userList = usersList.map((item)=>{
        return(
            {name: `${item.firstName} ${item.lastName}`, code: item.password}
        )
    })
    const customersNameList = customersList.map((item)=>{
        return(
            {name: item.companyName}
        )
    })

    useEffect(() => {
        getTeams();
    }, [])

    const addTeamFormik = useFormik({
        initialValues: {
            teamName: "",
            customerList:"",
            users: "",
        },
        validate: (data) => {
            let errors = {};

            if (!data.teamName) {
                errors.teamName = "Please enter team name.";
            }
            if (!data.users) {
                errors.users = "Please select users.";
            }

            return errors;
        },
        onSubmit: (data) => {
            console.log("form submitted !");
        },
    });
    const isAddTeamFormFieldValid = (name) =>
        !!(addTeamFormik.touched[name] && addTeamFormik.errors[name]);
    const getAddTeamFormErrorMessage = (name) => {
        return (
            isAddTeamFormFieldValid(name) && (
                <small className="p-error">{addTeamFormik.errors[name]}</small>
            )
        );
    };

    // ============Code for get Status==========//
    const getStatus = (rowdata) => {
        return (
            <span
                // onClick={(event) => statusConfirmation(event, rowdata)}
                className={
                    rowdata.status === "Active" ? "badge bg-success" : "badge bg-danger"
                }
            >
                {rowdata.status}
            </span>
        );
    };
    // ============Edit button for update form===========//
    const getActionuttons = (rowdata) => {
        return (
            <CTooltip content="Edit" placement="right">
                <span onClick={() => {
                    setViewMode(1);
                    setEditMode(2);
                }}
                >
                    <FaEdit
                        className="act-btn"
                        style={{ color: "#6777ef", cursor: "pointer" }}
                    />
                </span>
            </CTooltip>
        );
    };
    const getActionuttons1 = (rowdata) => {
        return (
            <CTooltip content="Edit" placement="right">
                <span >
                    <Button
                        // label="Edit"
                        icon="pi pi-pencil"
                        className="p-button-outlined p-button-sm"
                        onClick={() => {
                            setViewMode(1);
                            setEditMode(2)
                        }}
                    />
                </span>
            </CTooltip>
        );
    };

    //=== get all teams =====
    const getTeams = async () => {
        setshowspinner(true);
        await axios
            .get(`${AppConstants.Api_BaseUrl}admin/AccountTeam/getAll`,
                {
                    headers: {
                        // "Content-Type": "application/x-www-form-urlencoded;",
                    },
                })
            .then(
                (res) => {
                    const dt = res.data;
                    setTeamData(dt);
                    setshowspinner(false);
                },
                (err) => {
                    console.log(err);
                    setshowspinner(false);
                }
            )
    }



    const renderHeader1 = () => {
        return (
            <div className="row d-flex">
                <div className="col-lg-8">
                    <form
                    // onSubmit={searchFormik.handleSubmit}
                    >
                        <div className="row gap-lg-0 mt-lg-0">
                            <div className="col-sm-12 col-lg-4 mb-1">
                                <span className="p-input-icon-left w-100">
                                    <i className="pi pi-search" />
                                    <InputText
                                        // value={globalFilterValue1}
                                        // onChange={onGlobalFilterChange1}
                                        placeholder="Search name"
                                        className="p-inputtext-sm w-100"
                                        optionLabel="name"
                                        optionValue="name"
                                        filter={false}
                                    />
                                </span>
                            </div>
                            <div className="col-sm-12 col-lg-4 mb-1">
                                <Dropdown
                                    id="status"
                                    name="status"
                                    options={status}
                                    // value={searchFormik.values.status}
                                    // onChange={searchFormik.handleChange}
                                    className="p-inputtext-sm w-100"
                                    optionLabel="name"
                                    optionValue="name"
                                    filter={false}
                                    placeholder="Status"
                                />
                            </div>
                        </div>
                    </form>
                </div>
                <div
                    className="col-sm-12 col-md-12 col-lg-4 d-flex "
                    style={{ justifyContent: "right" }}
                >
                    <Button
                        label="Add Team"
                        className="p-button-outlined p-button-primary p-button-md"
                        onClick={() => {
                            setViewMode(1);
                            setEditMode(1)
                        }}
                    />
                    <Button
                        style={{
                            marginLeft: "10px",
                        }}
                        role="button"
                        icon="pi pi-filter-slash"
                        label="Clear"
                        className="p-button-outlined p-button-md"
                    // onClick={() => {
                    //   searchFormik.resetForm();
                    //   initFilters1();
                    //   if (pageNumber !== 1) {
                    //     setPageNumber(1);
                    //   } else {
                    //     getAllAccounts({
                    //       dateRange: null,
                    //       status: "",
                    //       page: 1,
                    //     });
                    //   }
                    // }}
                    />
                </div>
            </div>
        );
    };
    const header1 = renderHeader1();

    // ======= add team form ========
    const addTeamForm = () => {
        return (
            <form onSubmit={addTeamFormik.handleSubmit}>
                <div className='mx-3'>
                    <div className="row">
                        <div className=" col-sm-12 col-md-12 col-lg-12 mt-4">
                            <span className="p-float-label">
                                <InputText
                                    id="teamName"
                                    value={addTeamFormik.values.teamName}
                                    onChange={addTeamFormik.handleChange}
                                    className={
                                        (classNames({
                                            "p-invalid": isAddTeamFormFieldValid("teamName"),
                                        }),
                                            "p-inputtext-sm w-100 borderClass")
                                    }
                                />
                                <label htmlFor="teamName">Team Name</label>
                            </span>
                            {getAddTeamFormErrorMessage("teamName")}
                        </div>
                        <div className=" col-sm-12 col-md-12 col-lg-12 mt-5">
                            <span className="p-float-label">
                                <MultiSelect
                                    id="users"
                                    inputId="multiselect"
                                    value={addTeamFormik.values.users}
                                    options={userList}
                                    onChange={addTeamFormik.handleChange}
                                    className={
                                        (classNames({
                                            "p-invalid": isAddTeamFormFieldValid("skills"),
                                        }),
                                            "p-inputtext-sm w-100 borderClass")
                                    }
                                    optionLabel="name"
                                    display="chip"
                                />
                                <label htmlFor="users">Users</label>
                            </span>
                            {getAddTeamFormErrorMessage("users")}
                        </div>
                    </div>
                </div>
                <div className="modal-footer d-flex justify-content-end my-3 mx-3">
                    <Button
                        label={editMode === 1 ? "Add" : "Update"}
                        type="submit"
                        className="bg-primary border-0  p-button-md  btn-color p-button-raised"
                    />
                    <Button
                        onClick={() => {
                            setViewMode(0);
                            addTeamFormik.resetForm();
                        }}
                        label={"Cancel"}
                        style={{ marginLeft: "10px" }}
                        className="bg-danger border-0 p-button-md p-button-raised"
                    />
                </div>
            </form>
        )
    }

    return (
        <div className="row mb-2">
            {viewMode === 1 ? (addTeamForm()) :
                viewMode === 2 ? (showRowDetails()) :
                    (<DataTable
                        value={teamData}
                        responsiveLayout="scroll"
                        className="p-datatable-customers"
                        showGridlines={false}
                        rows={10}
                        stripedRows
                        dataKey="id"
                        paginator
                        // filters={filters1}
                        // loading={accountLoading}
                        filterDisplay="menu"
                        globalFilterFields={[
                            "email",
                            "user",
                            "role",
                            "updatedDate",
                            "status",
                        ]}
                        header={header1}
                        emptyMessage="No teams found."
                        onRowClick={() => {
                            setViewMode(1);
                            setEditMode(2)
                        }}
                    >
                        <Column
                            field="teamName"
                            header="Team Name"
                            style={{ cursor: "pointer" }}
                            sortable
                        ></Column>
                        <Column
                            header="Customers"
                            field="customerName"
                            style={{ cursor: "pointer" }}
                            sortable
                        ></Column>
                        <Column
                            header="Users"
                            field="userName"
                            style={{ cursor: "pointer" }}
                            sortable
                        ></Column>
                        {/* <Column
                            field="status"
                            header="Status"
                            body={getStatus}
                            style={{ cursor: "pointer" }}
                            sortable
                        ></Column> */}
                    </DataTable>)}
            {showspinner && (
                <div className="spinner-div">
                    <ProgressSpinner />
                </div>
            )}
        </div>
    )
}

export default Team;